.form {
  position: absolute;
  top: 25%;
  right: 25%;
  height: auto;
  background: rgba(0, 0, 0, 0.507);
  padding: 5vw;
  border: none;
  border-radius: 10px;
  margin: auto;
  width: 50%;
  padding: 10px;
}
.img_bg {
  position: absolute;
  width: 100%;
  height: 100%;
}
.inp {
  width: 90%;
  margin: 5%;
  height: 30px;
  border: none;
  color: black;

  text-align: center;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.767);
}
.sbtn {
  border-radius: 10px;
  border: none;
  background: #07b966;
  width: 40%;
  font-weight: bold;
  color: white;
  margin-left: 30%;
  margin-right: 30%;
}
.title {
  position: absolute;
  top: 0%;
  left: 0px;
  right: 0px;
  font-size: 3vw;
  margin-left: 0%;
  margin-right: 0%;
  width: auto;
  background: rgba(0, 0, 0, 0.767);
  border: none;
  padding: 5px;
}
.mh {
  font-size: 4vw;
}
.status-txt {
  width: 90%;
  margin: 5%;
  border: none;
  color: beige;
  text-align: center;
  border-radius: 10px;
}
